            alt=""
            style="width: 25px; margin-left: 10px"
          />
        </div>
      </div>
      <img src="../../../assets/image/login/miniLogo.png" alt="" class="frog" />
    </div>
    <div class="right">
      <div class="content">
        <img
          class="logo"
          src="../../../assets/image/login/camptogoLogo.png"
          alt="logo"
        />
        <div class="form">
          <div class="form-item">
            <p>请输入用户名</p>
            <input
              type="text"
              v-model="username"
              @keyup.enter="$refs.passwordInput.focus()"
              ref="autofocusInput"
            />
          </div>
          <div class="form-item">
            <div
              style="
                display: flex;
                flex-direction: row;
                justify-content: space-between;
              "
            >
              <p>请输入密码</p>
              <p style="color: #000; cursor: pointer" @click="onClick">
                忘记密码?
              </p>
            </div>
            <input
              type="password"
              id="password"
              v-model="password"
              @keyup.enter="loginBtnClick"
              ref="passwordInput"
            />
            <img
              v-show="!showPassword && password !== ''"
              class="hidePassword"
              src="../../../assets/image/providerTools/orderManagement/hide.png"
              @click="showPassword = !showPassword"
            />
            <img
              v-show="showPassword && password !== ''"
              class="showPassword"
              src="../../../assets/image/providerTools/orderManagement/show.png"
              @click="showPassword = !showPassword"
            />
          </div>
          <div class="buttonZone">
            <button id="loginBtn" @click="loginBtnClick">
              <!-- 登&nbsp;&nbsp;录 -->
              立即登录
            </button>
          </div>
