            >
          </div>
        </div>
      </div>
      <a :href="homeHerf" class="backButton">返回官网</a>
    </div>
    <div class="fixedFullLoading" v-show="isShowDialog">
      <FindAccount
        v-show="isShowDialog"
        @success="passwordChanged"
        @close="closePop"
      ></FindAccount>
      <!-- <refind v-show="isShowDialog" @click="isShowDialog = true"></refind> -->
      <div
        @click="isShowDialog = false"
        style="width:100%;height:100%;z-index=2;"
      ></div>
    </div>
