<script>
import { $login } from "../../../network/auth/auth";
import { encode_ez } from "../../../common/utils";
import refind from "./refind.vue";
import { decode_ez } from "../../../common/utils";
import FindAccount from "./FindAccount.vue";
export default {
  name: "e-Login",
  beforeRouteEnter(to, from, next) {
    // ...
    next();
  },
  setup() {
    return {
      homeHerf: `${location.origin}`,
    };
  },
  created() {
    this.$nextTick(() => {
      this.$refs.autofocusInput.focus();
    });
  },
  data() {
    return {
      username: "",
      password: "",
      showPassword: false,
      isShowDialog: false,
    };
  },
  methods: {
    passwordChanged(e) {
      // const {username,password}= e.detail[0];
      // this.username=username
      // this.password=password
      this.isShowDialog = false;
    },
    closePop() {
      this.isShowDialog = false;
      console.log("emit!!!");
    },
    goto() {
      location.href = `${location.origin}/dynamic.html/infoCollect`;
    },
    async loginBtnClick() {
      if (!this.username) {
        this.$toast.warn("请输入用户名");
        return;
      }
      if (!this.password) {
        this.$toast.warn("请输入密码");
        return;
      }
      const password_encoded = encode_ez(this.password);
      const requestPayload = {
        username: this.username,
        password: password_encoded,
      };
      let response;
      try {
        response = await $login(requestPayload);
        console.log("login res: ", response);
        let userid = response.details.id;
        this.$store.commit("providerUser/setUserId", userid, { root: true });
        const { Code, msg } = response;
        if (Code !== 200 || msg !== "success") {
          this.$toast.error("用户名或密码错误");
          return;
        }
      } catch (err) {
        this.$toast.error("登录失败");
        return;
      }
      let userInfo = {
        mobile: decode_ez(response.details.mobile),
        mail: response.details.email,
        date_created: response.details.date_created,
      };

      console.log(userInfo);

      this.$store.commit("providerUser/setUserInfo", userInfo);
      window.localStorage.setItem("userinfo", JSON.stringify(userInfo));

      const { details } = response;

      if (details) {
        const {
          id,
          username,
          auth_token,
          is_staff,
          is_superuser,
          is_provider,
        } = details;
        // 构造json对象，里面是要保存为cookie的信息
        /* const loginInfo = {
          userId: id,
          loginName: username,
          authToken: auth_token,
          isStaff: is_staff,
          isSuperuser: is_superuser,
          isProvider: is_provider,
        }
        this.cookieOperations.setCookie(loginInfo); */

        /* this.cookieOperations.setCookie({
          xxxxxxx: window.JSON.stringify({
            id: '0000',
            idd: 11111,
            iddd: [1,2,3,4,5],
          }),
          id: '12334',
        }); */

        // 使用localStorage存储登录信息
        window.localStorage.setItem("userID", id);
        window.localStorage.setItem("userName", username);
        window.localStorage.setItem("authToken", auth_token);
        window.localStorage.setItem("isStaff", is_staff);
        window.localStorage.setItem("isSuperUser", is_superuser);
        window.localStorage.setItem("isProvider", is_provider);

        if (details.hasOwnProperty("provider")) {
          // window.localStorage.setItem("providerID", details.provider.id);
        }

        console.log(is_staff);
        console.log(is_provider);

        if (is_staff) {
          // await this.$router.replace("/tools/admin");
          await this.$router.push({ name: "AdminCategory" });
        } else if (is_provider) {
          window.localStorage.setItem("firstTimeLogin", 1);
          await this.$router.replace("/tools/provider/settings/update");
        } else {
          this.$toast.error("未找到该用户的角色信息");
        }
        // this.$toast.success('登录成功');
      } else {
        this.$toast.error("用户名或密码错误");
      }
    },
    onClick() {
      this.isShowDialog = true;
    },
  },
  watch: {
    showPassword(newVal) {
      if (newVal) {
        document.getElementById("password").setAttribute("type", "text");
      } else {
        document.getElementById("password").setAttribute("type", "password");
      }
    },
  },
  components: {
    refind,
    FindAccount,
  },
};
</script>

