export function processDate(text) {
  const arr = text.split('').slice(0, 10);
  arr[4] = '.';
  arr[7] = '.'
  return arr.join('');
}

export function processImageUrl(imageUrl) {
  const arr = imageUrl.split('/');
  return arr[2];
}

export function checkInputText(text) {
  let result;
  const arr = text.split('');
  if(arr.length === 0) {
    result = true;
  }
  else {
    result = /^[0-9]+(\.[0-9]{1,2})?$/.test(text); // 限3位小数
    if(result) {
      if(arr.length >= 2 && arr[0] === '0' && arr[1] !== '.') {
        result = false;
      }
    } 
  }
  return result;
}

export function formatTime(year, month, date, hour, minute, second) {
  //console.log(hour+' '+minute)
  const y = year;
  const m = Number(month) < 10? '0'+month: month;
  const d = Number(date) < 10? '0'+date: date;
  const h = Number(hour) < 10? '0'+hour.slice(1, 2): hour;
  const min = Number(minute)<10? '0'+minute.slice(1, 2): minute;
  const s = Number(second)<10? '0'+second.slice(1, 2): second;
  return `${y}-${m}-${d}T${h}:${min}:${s}Z`;
}

const setTransparentBg = (el) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      el.style.backgroundColor = 'transparent';
      resolve();
    }, 200);
    
  });
}
const setRedBg = (el) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      el.style.backgroundColor = 'red';
      resolve();
    }, 200);
    
  });
}
export async function warnBling(el) {
  el.style.backgroundColor = 'red';
  await setTransparentBg(el);
  await setRedBg(el);
  await setTransparentBg(el);
  await setRedBg(el);
  await setTransparentBg(el);
  return new Promise((resolve) => {
    resolve();
  });
}

// 判断是否是一个对象
function isObject(object) {
  return object !== null && typeof object === 'object';
}
// 判断是否是一个数组
function isArray(array) {
  return array !== null && typeof array === 'array';
}
// 判断两个对象是否有完全一样的keys&value【且顺序一致】
export function objectDeepEqualDetect(obj1, obj2) {
  const keys1 = Object.keys(obj1); // 由obj1的键名组成的数组
  const keys2 = Object.keys(obj2); // 由obj2的键名组成的数组
  if(keys1.length !== keys2.length) {
    return false;
  }
  for(let i = 0; i < keys1.length; i++) {
    const val1 = obj1[keys1[i]];
    const val2 = obj2[keys2[i]];
    const type1 = typeof val1;
    const type2 = typeof val2;
    if(type1 !== type2) {
      return false;
    }
    else {
      if(type1 === 'object' && type2 === 'object') {
        const f = objectDeepEqualDetect(val1, val2);
        if(!f) {
          return false;
        }
      }
      else if(type1 === 'array' && type2 === 'array') {
        const f = arrayDeepEqualDetect(val1, val2);
        if(!f) {
          return false;
        }
      }
      else {
        if(val1 !== val2) {
          return false;
        }
      }
    }
  }
  return true;
}
// 判断两个数组是否有完全一样的value（有顺序，顺序得一致）
export function arrayDeepEqualDetect(arr1, arr2) {
  if(arr1.length !== arr2.length) return false;
  for(let i = 0;i < arr1.length; i++) {
    if(typeof arr1[i] !== typeof arr2[i]) {
      return false;
    }
    else {
      if(typeof arr1[i] === 'object' && typeof arr2[i] === 'object'){
        const f = objectDeepEqualDetect(arr1[i], arr2[i]);
        if(!f) {
          return false;
        }
      }
      else if(typeof arr1[i] === 'array' && typeof arr2[i] === 'array') {
        const f = arrayDeepEqualDetect(arr1[i], arr2[i]);
        if(!f) {
          return false;
        }
      }
      else {
        if(arr1[i] !== arr2[i]) {
          return false;
        }
      }
    }
  }
  return true;
}

export const processStringAsArray = (text) => {
  const arr = text.split('\n');
  // 去掉空字符串
  const noemptyarr = arr.filter(item => item && item.trim());
  // 对非空字符串做去头去尾处理
  for(const index in noemptyarr) {
    noemptyarr[index] = noemptyarr[index].trim();
  }
  return noemptyarr;
}

export function readImageFileToDetectRatio(file, idealRatio, deviation = 0.2) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    // 读取文件发生错误时触发
    reader.onerror = () => {
      reject({
        code: '0000',
        msg: `图片读取失败，错误码为${reader.error.code}`
      });
    }
    
    // 完全读取文件时触发
    reader.onload = () => {
      const image = new Image();
      image.src = reader.result; // needed
      image.onload = () => {
        const {
          width, height
        } = image;
        const ratio = width / height;
        if(ratio < idealRatio - deviation || ratio > idealRatio + deviation) {
          resolve({
            code: '0010',
            msg: '图片长宽比例不符要求'
          });
        }
        else {
          resolve({
            code: '1111',
            msg: '图片长宽比例符合要求'
          });
        }
      }
    }
  })
}

export function uniqueArr(arr) {
  const array = [];
  for(let i = 0; i < arr.length; i++) {
    if(array.indexOf(arr[i]) === -1) {
      array.push(arr[i]);
    }
  }
  return array;
}

export function processTimeToMinInProviderOrderShow(time) {
  const t1 = time.replace(/-/g, '.');
  const t2 = t1.replace('T', ' ');
  return t2.slice(0, 16);

}

export function checkPasswordComplexity(password) {
  let complexity = 0;
  if(password.match(/([0-9])+/)) complexity++;
  if(password.match(/([a-z])+/)) complexity++;
  if(password.match(/([A-Z])+/)) complexity++;
  return complexity;
}

export function deleteFileNameSuffix(fileName) {
  return fileName.substring(0, fileName.lastIndexOf('.'));
}

export function uniqueArray(arr) {
  const newArr = [];
  for(let i = 0; i < arr.length; i++) {
    if(newArr.indexOf(arr[i]) === -1) {
      newArr.push(arr[i]);
    }
  }
  return newArr;
}

export function calculateStringByteLength(str) {
  let b = 0;
  const charlen = str.length;  //初始化字节数递加变量并获取字符串参数的字符个数
  if(charlen !== 0) {  //如果存在字符串，则执行计划
      for(var i = 0; i < charlen; i ++) {  //遍历字符串，枚举每个字符
          if(str.charCodeAt(i) > 255) {  //字符编码大于255，说明是双字节字符
              b += 2;  //则累加2个
          }else {
              b ++;  //否则递加一次
          }
      }
      return b;  //返回字节数
  } else {
      return 0;  //如果参数为空，则返回0个
  }
}

export function findDimensions() {
  let winWidth, winHeight;
  //获取窗口宽度
  if (window.innerWidth)
  winWidth = window.innerWidth;
  else if ((document.body) && (document.body.clientWidth))
  winWidth = document.body.clientWidth;
  //获取窗口高度
  if (window.innerHeight)
  winHeight = window.innerHeight;
  else if ((document.body) && (document.body.clientHeight))
  winHeight = document.body.clientHeight;
  //通过深入Document内部对body进行检测，获取窗口大小
  if (document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth)
  {
  winHeight = document.documentElement.clientHeight;
  winWidth = document.documentElement.clientWidth;
  }
  //结果输出至两个文本框
  return [
    winWidth,
    winHeight
  ]
}
