<template>
  <div class="fa-container">
    <div class="title">
      <div>找回密码</div>
      <div class="closePop" @click="close">+</div>
    </div>
    <div class="form">
      <div class="label">
        <input
          type="text"
          v-model="value[0]"
          @input="onInput(0)"
          class="account"
          placeholder="请输入注册手机/注册邮箱"
        />
        <div class="error" v-if="verifyState[0]">输入有误</div>
      </div>
      <div class="label">
        <input
          :type="type(0)"
          v-model="value[1]"
          @input="onInput(1)"
          class="passwordNew"
          placeholder="新密码"
          maxlength="16"
        />
        <div class="imgWrapper">
          <img
            v-if="!passwordState[0]"
            class="uniImg"
            src="../../../assets/image/providerTools/orderManagement/hide.png"
            alt=""
            @click="setType(0)"
          />
          <img
            v-if="passwordState[0]"
            @click="setType(0)"
            class="uniImg"
            src="../../../assets/image/providerTools/orderManagement/show.png"
            alt=""
          />
        </div>
        <div class="error" v-if="verifyState[1]">
          密码长度6-20位,同时包含大、小写字母和数字
        </div>
      </div>
      <div class="label">
        <input
          :type="type(1)"
          v-model="value[2]"
          @input="passwordCheck"
          class="passwordConfirm"
          placeholder="请再次确认新密码"
          maxlength="16"
        />
        <div class="imgWrapper">
          <img
            v-if="!passwordState[1]"
            @click="setType(1)"
            class="uniImg"
            src="../../../assets/image/providerTools/orderManagement/hide.png"
            alt=""
          />
          <img
            v-if="passwordState[1]"
            @click="setType(1)"
            class="uniImg"
            src="../../../assets/image/providerTools/orderManagement/show.png"
            alt=""
          />
        </div>
        <div class="error" v-if="verifyState[2]">密码不一致或不符合要求</div>
      </div>
      <div class="label codeBox">
        <input
          type="text"
          class="code"
          v-model="value[3]"
          @input="onInput(3)"
          placeholder="验证码"
          maxlength="6"
        />
        <button
          :class="{ disabled: codeState }"
          class="getCode"
          @click="sendCode"
        >
          {{ state }}
        </button>
        <div class="error" v-if="verifyState[3]">验证码输入有误</div>
      </div>
    </div>
    <div class="confirm">
      <common-button93d500 @click="postPassword">重置密码</common-button93d500>
      <div class="contact">其他问题，请联系营探客服：partner@camptogo.com</div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import request from "../../../../dynamic/request";
import { encode_ez } from "../../../common/utils";
export default {
  setup() {
    const state = ref("获取验证码");
    const codeState = ref(false);
    const passwordState = ref([false, false]);
    const email = (content) =>
      /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.]){1,2}[A-Za-z\d]{2,5}$/.test(
        content
      );
    const phone = (content) => /^[1][3,4,5,7,8][0-9]{9}$/.test(content);
    const verify = [
      (content) => email(content) || phone(content),
      (content) => /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/.test(content),
      (content, last) =>
        /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/.test(content) &&
        last == content,
      (content) => /^[0-9]{6}$/.test(content),
    ];
    const verifyState = ref(new Array(4).fill(false));
    const value = ref(new Array(4).fill(""));
    return {
      state,
      codeState,
      passwordState,
      verify,
      verifyState,
      value,
      email,
      phone,
    };
  },
  methods: {
    close() {
      this.$emit("close", {});
      // console.log("close");
    },
    async sendEmail() {
      if (this.codeState) {
        return;
      }
      const ret = await request({
        url: "/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/base/funcs/send/provider/sms/email",
        method: "POST",
        data: {
          email: this.value[0],
        },
      });
      if (ret.Code == 200) {
        this.$toast.success("发送成功,请查收");
        return true;
      } else {
        this.$toast.error("未注册或验证码已过期");
        return false;
      }
    },
    sendMessage() {},
    async postPassword() {
      if (
        this.verifyState.some((r) => r) ||
        this.value.some((r) => r.length == 0)
      ) {
        this.$toast.warn("信息输入有误");
        return;
      }
      const email = this.value[0];
      const password = encode_ez(this.value[2]);
      const sms_code = this.value[3];
      const ret = await request({
        url: "/api/e9b849a515a84327b424af7ccdbf2949/web/v1_0_0/provider/reset/password",
        data: { email, password, sms_code },
        method: "POST",
      });
      console.log(ret);
      if (ret.Code != 200) {
        this.$toast.error("验证码错误");
      } else {
        this.$toast.success("修改成功");
        this.$emit("success", {
          username: this.value[0],
          password: this.value[2],
        });
      }
    },
    onInput(index) {
      if (this.verify[index](this.value[index])) {
        this.verifyState[index] = false;
      } else {
        this.verifyState[index] = true;
      }
    },
    passwordCheck() {
      const content = this.value[2];
      const last = this.value[1];
      this.verifyState[2] = !this.verify[2](content, last);
    },
    type(index) {
      if (this.passwordState[index]) {
        return "text";
      } else {
        return "password";
      }
    },
    setType(index) {
      this.passwordState[index] = !this.passwordState[index];
    },
    async sendCode() {
      if (this.codeState) {
        return;
      }
      if (this.email(this.value[0])) {
        const state = await this.sendEmail();
        if (!state) return;
      } else if (this.phone(this.value[0])) {
        // send message
        this.$toast.warn("请输入邮箱,手机号暂不支持");
        return;
      } else {
        this.$toast.warn("手机号或邮箱输入有误");
        return;
      }
      this.codeState = true;
      this.state = `${60}s后重试`;
      let timer = 59;
      const a = setInterval(() => {
        this.state = `${timer}s后重试`;
        timer--;
        if (timer == 0) {
          clearInterval(a);
          this.codeState = false;
          this.state = "获取验证码";
        }
      }, 1000);
    },
  },
};
</script>

